.footer {
  padding: 15px 0 72px;
  background-color: #fff;
  text-align: center; }
  .footer .ui-inner.none-top {
    padding-top: 0; }
  .footer .ui-inner.none-bottom {
    padding-bottom: 0; }
  .footer .ui-inner.min {
    padding: 9px 12px; }
  .footer .ui-inner.side {
    padding: 0px 15px; }
  .footer .ui-buttons {
    font-size: 0;
    margin-bottom: 10px; }
    .footer .ui-buttons .quarter {
      width: 25%;
      padding-right: 4px;
      display: inline-block;
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
              box-sizing: border-box; }
    .footer .ui-buttons .mon-button {
      font-size: 13px;
      color: #494949;
      width: 100%;
      height: 34px;
      line-height: 34px;
      -webkit-border-radius: 1px;
         -moz-border-radius: 1px;
              border-radius: 1px;
      border: 1px solid #ced3d7;
      letter-spacing: -1px;
      padding: 0;
      vertical-align: middle;
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
              box-sizing: border-box; }
  .footer .footer_info {
    margin-top: 20px; }
    .footer .footer_info a {
      display: inline-block;
      color: #888888;
      font-size: 13px; }
      .footer .footer_info a:nth-child(2):before {
        content: "";
        margin: 0 8px;
        display: inline-block;
        width: 3px;
        height: 3px;
        -webkit-border-radius: 50%;
           -moz-border-radius: 50%;
                border-radius: 50%;
        background-color: #e9e9e9;
        vertical-align: 2px; }
      .footer .footer_info a:nth-child(2):after {
        content: "";
        margin: 0 8px;
        display: inline-block;
        width: 3px;
        height: 3px;
        -webkit-border-radius: 50%;
           -moz-border-radius: 50%;
                border-radius: 50%;
        background-color: #e9e9e9;
        vertical-align: 2px; }
  .footer .copyright {
    position: relative;
    margin: 20px 0;
    width: 100px; }
    .footer .copyright img {
      width: 74px;
      vertical-align: middle; }
    .footer .copyright .ico-footer-arrow {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      margin: 0 3px;
      width: 8px;
      height: 6px; }
  .footer address {
    color: #a9a9a9;
    font-size: 12px; }
    .footer address span {
      display: block;
      text-align: center;
      letter-spacing: -0.8px;
      margin: 2px 0; }
